.zalog-wrapper{
  float: left;
  width: 100%;
  background-image: url(../img/zalog-bg.jpg);
  background-repeat: no-repeat;
  background-position: 152px 26px;
  padding-bottom: 72px;

  @include breakpoint(max-width 1200px){
    background-position: 15px 26px;
  }
  @include breakpoint(max-width 979px){
    background-position: 0px 125px;
    background-size: 190px;
  }
  @include breakpoint(max-width 767px){
    background: none;
  }
  @include breakpoint(max-width 479px){
    padding-bottom: 40px;
  }

  .zalog{
    width: 560px;
    margin: 0 auto;
    color: #191919;
    text-align: center;

    @include breakpoint(max-width 767px){
      width: 100%;
    }

    h2{
      font-size: 29px;
      font-family: Lato-Black;
      margin-top: 97px;
      margin-bottom: 0;

      @include breakpoint(max-width 979px){
        margin-top: 60px;
      }
      @include breakpoint(max-width 767px){
        font-size: 24px;
      }
      @include breakpoint(max-width 479px){
        font-size: 22px;
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 20px;
      }
    }
    p{
      font-size: 16px;

      @include breakpoint(max-width 479px){
        font-size: 14px;
      }

      &:nth-child(2){
        margin-top: 11px;
        margin-bottom: 38px;
      }
      &:nth-child(3){
        text-transform: uppercase;
        margin-bottom: 21px;
      }
    }

    a{
      background-color: #ffc80a;
      color: #191919;
      font-size: 18px;
      width: 285px;
      font-family: Lato-Black;
      text-align: center;
      height: 46px;
      border-radius: 25px;
      @include display(flex);
      @include justify-content(center);
      @include flex-direction(column);
      @include transition(all .3s ease-out);
      margin-left: auto;
      margin-right: auto;
      @include box-shadow(0 0 10px rgba(255,193,0, 1));

      &:hover{
        @include box-shadow(0 3px 4px rgba(0,0,0,.3));
      }
    }
  }
}