.usloviya-wrapper{
  background-image: url(../img/usloviya.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  float: left;
  width: 100%;
  padding-bottom: 93px;
  -webkit-background-size: 100%;
  background-size: 100%;

  @include breakpoint(max-width 1200px){
    -webkit-background-size: auto;
    background-size: auto;
  }
  @include breakpoint(max-width 479px){
    padding-bottom: 35px;
  }

  h2{
    font-size: 29px;
    color: #191919;
    font-family: Lato-Black;
    text-align: center;
    margin-top: 61px;
    margin-bottom: 19px;

    @include breakpoint(max-width 767px){
      font-size: 24px;
    }
    @include breakpoint(max-width 479px){
      font-size: 22px;
      width: 90%;
      margin-top: 25px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  p{
    color: #191919;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    max-width: 620px;
    margin: 0 auto 32px;
    line-height: 21px;

    @include breakpoint(max-width 767px){
      width: 90%;
    }
    @include breakpoint(max-width 479px){
      font-size: 14px;
    }
  }
  a{
    background-color: #ffc80a;
    color: #191919;
    font-size: 16px;
    width: 285px;
    font-family: Lato-Black;
    text-align: center;
    height: 46px;
    border-radius: 25px;
    @include display(flex);
    @include justify-content(center);
    @include flex-direction(column);
    @include transition(all .3s ease-out);
    margin-left: auto;
    margin-right: auto;
    @include box-shadow(0 0 10px rgba(255,193,0, 1));

    &:hover{
      @include box-shadow(0 3px 4px rgba(0,0,0,.3));
    }
  }
}